import React from 'react';
import styled from '@emotion/styled';
import { graphql, Link } from 'gatsby';
import Layout from '../components/layout';
import Preview from '../components/preview';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';


const BlogList = ({
  data,
  classes,
  pageContext,
  location,
}) => {
  const posts = data.allMarkdownRemark.edges;
  return (
    <Layout>
      {posts.map(({ node }) => (
        <Preview
          key={node.fields.slug}
          title={node.frontmatter.title || node.fields.slug}
          url={node.fields.slug}
          summary={node.frontmatter.description || node.excerpt}
          category={node.frontmatter.category}
          date={node.frontmatter.date}
          cover={node.frontmatter.cover}
        />
      ))}
      <Link
        to={
          `/blog${pageContext.current <= 1
            ? ''
            : `/${pageContext.current}`
          }`}
      >
        <Button
          disabled={pageContext.current === 0}
        >
          이전 페이지
        </Button>
      </Link>
      <Link
        to={pageContext.current + 1 === pageContext.numPages ?
          `${location.pathname}` :
          `/blog/${pageContext.current + 2}`
        }
      >
        <Button
          disabled={pageContext.current + 1 === pageContext.numPages}
        >
          다음 페이지
        </Button>
      </Link>
    </Layout>
  );
};

export default BlogList;

export const blogListQuery = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            description
            date
            category
            cover {
              childImageSharp {
                resolutions(height: 320) {
                  src
                }
              }
            }
          }
        }
      }
    }
  }
`;
