import React, { Fragment } from 'react';
import styled from '@emotion/styled';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';

import { rhythm } from '../../utils/typography';

const titleColor = '#535353';
const categoryColor = '#7e7e7e';
const contentColor = '#707070';
const dateColor = '#a2a2a2';

const Article = styled.article`
  display: flex;
`;

const Wrapper = styled.div`
  flex: 1;
`;

const Category = styled.h3`
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1.375px;
  text-transform: uppercase;
  color: ${categoryColor};
  margin-bottom: 8px;
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: bold;
  color: ${titleColor};
  letter-spacing: -0.72px;
  line-height: 32px;
  margin-bottom: 8px;
  
  a {
    color: inherit;
    text-decoration: none;
  }
`;

const Content = styled.summary`
  font-size: 16px;
  color: ${contentColor};
  margin-bottom: 8px;
`;

const Date = styled.time`
  font-size: 14px;
  color: ${dateColor};
  letter-spacing: -0.2px;
`;

const PreviewImage = (({ image, ...props }) => (
  <Link
    css={{
      width: '160px',
      marginLeft: '24px',
      backgroundImage: `url(${image})`,
      backgroundPosition: 'center',
      backgroundSize: 'cover',
    }}
    {...props}
  />
));

const Preview = ({
  url,
  title,
  date,
  summary,
  category,
  cover,
  ...props,
}) => (
  <Article {...props}>
    <Wrapper>
      <Category>{category}</Category>
      <Title>
        <Link to={url}>
          {title}
        </Link>
      </Title>
      <Content dangerouslySetInnerHTML={{ __html: summary }} />
      <Date>{date}</Date>
    </Wrapper>
    { cover && (
      <PreviewImage
        to={url}
        image={cover.childImageSharp.resolutions.src}
      />
    )}
  </Article>
);

Preview.propTypes = {
  url: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  category: PropTypes.string.isRequired,
  cover: PropTypes.object,
};

Preview.defaultProps = {
  cover: null,
};

export default Preview;
